footer {
    a {
        color: white;
    }
    .nav {
        img {
            transition: all ease 300ms;
            &:hover {
                opacity: 0.5;
            }
            @include lg {
                width: 24px;
            }
        }
        @include md {
            float: right;
        }
    }
    .nav__menu {
        position: relative;
        a {
            &::after {
                content: '';
                position: absolute;
                left: 50%;
                bottom: -3px;
                height: 3px;
                width: 0;
                background-color: $light;
                transition: all 0.25s ease;
                transform: translate(-50%);
            }
            &:hover::after {
                width: 100%;
            }
        }
    }
    .info {
        padding-bottom: 22px;
        border-bottom: 1px solid white;
        line-height: 48px;
        font-size: 42px;
        @include lg {
            padding-bottom: 27px;
            line-height: 26px;
            font-size: $font-size-h6;
        }
        @include md {
            padding-bottom: $spacer-6;
            line-height: 21px;
            margin-bottom: 18px;
            font-size: $font-size-m;
        }
        a:hover {
            opacity: 0.5;
        }
    }
    .info__list {
        margin-bottom: 17px;
        @include lg {
            margin-bottom: 11px;
        }
        @include md {
            margin-bottom: 7px;
        }
        &:first-child {
            img {
                padding: 0 6px;
                @include lg {
                    padding: 0 5px;
                }
                @include md {
                    padding: 0 2px;
                }
            }
        }
        &__icon {
            margin-right: 14px;
            @include lg {
                width: 28px;
                margin-right: 10px;
            }
            @include md {
                width: 16px;
            }
        }
    }
    .copyright {
        &__item {
            margin-left: 62px;
            &:first-child {
                margin-left: 0;
            }
            @include lg {
                margin-left: $spacer-6;
            }
            @include md {
                margin-left: 0;
            }
        }
        p {
            @include md {
                margin-bottom: 7px;
            }
        }
        a:hover {
            opacity: .5;
        }
    }
    .logo {
        margin-right: 38px;
        @include md {
            width: 46px;
            margin-right: 0;
        }
    }
}